.navbar-content {
	background-color: #f8f8f8;
	padding: 2px;
}

.nav-ul {
	display: flex;
	justify-content: flex-start;
	align-content: center;
	gap: 1em;
	margin-top: 1em;
}

a {
	text-decoration: none;
	color: hsl(0, 0%, 50%);
}

a:hover {
	color: hsl(0, 0%, 50%);
	font-weight: bold;
}
